.container {
  width: 100%;
  margin: 0 auto;
  padding: 0 15px;
}

.section {
  padding: 30px 0;
}

h1, h2, h3, h4, h5, h6, p, ul {
  margin: 0;
  padding: 0;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

.overlay {
  opacity: 0;
  visibility: hidden;
  width: 100%;
  height: 100%;
  z-index: 20;
  background-color: rgba(255, 145, 173, .5);
  transition: all .3s;
  position: fixed;
  top: 0;
  left: 0;
}

.modal {
  opacity: 0;
  visibility: hidden;
  width: 100%;
  max-width: 500px;
  max-height: 99vh;
  z-index: 999;
  text-align: center;
  background-color: #fff;
  border-radius: 3px;
  padding: 30px;
  transition: all .3s;
  position: fixed;
  top: 50%;
  left: 50%;
  overflow-y: auto;
  transform: translate(-50%, -50%);
  box-shadow: 0 3px 10px -.5px rgba(0, 0, 0, .2);
}

.modal.active, .overlay.active {
  opacity: 1;
  visibility: visible;
}

.modal__cross {
  width: 15px;
  height: 15px;
  fill: #444;
  cursor: pointer;
  position: absolute;
  top: 20px;
  right: 20px;
}

#background-wrap {
  z-index: -1;
  padding-top: 50px;
  display: none;
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}

.overlay.js-overlay-modal.active #background-wrap {
  display: block;
}

@keyframes animateCloud {
  0% {
    margin-left: -1000px;
  }

  100% {
    margin-left: 100%;
  }
}

.x1 {
  animation: animateCloud 35s linear infinite;
  transform: scale(.65);
}

.x2 {
  animation: animateCloud 20s linear infinite;
  transform: scale(.3);
}

.x3 {
  animation: animateCloud 30s linear infinite;
  transform: scale(.5);
}

.x4 {
  animation: animateCloud 18s linear infinite;
  transform: scale(.4);
}

.x5 {
  animation: animateCloud 25s linear infinite;
  transform: scale(.55);
}

.cloud {
  background: getColor(active-white);
  height: 120px;
  width: 350px;
  border-radius: 100px;
  position: relative;
  box-shadow: 0 8px 5px rgba(0, 0, 0, .1);
}

.cloud:after, .cloud:before {
  background: getColor(active-white);
  content: "";
  z-index: -1;
  position: absolute;
}

.cloud:after {
  height: 100px;
  width: 100px;
  border-radius: 100px;
  top: -50px;
  left: 50px;
}

.cloud:before {
  width: 180px;
  height: 180px;
  border-radius: 200px;
  top: -90px;
  right: 50px;
}

.header__item {
  color: getColor(accent);
}

.visually-hidden {
  white-space: nowrap;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
  -webkit-clip-path: inset(50%);
  clip-path: inset(50%);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

